import React, { useState, useEffect } from "react";
import { useWeb3React } from "@web3-react/core";
import { Contract } from "@ethersproject/contracts";
import { toast } from "react-toastify";
import { REDEEM_ADDRESS } from "../../constants";
import REDEEM_INFO from "../../constants/redeem.json";
import { formatUnits, parseUnits } from "@ethersproject/units";

function Redeem() {
  const { active, account, library } = useWeb3React();
  const [isProcessing, setIsProcessing] = useState(false);
  const [amount, setAmount] = useState(0);
  const [maxAmount, setMaxAmount] = useState(0);

  const getAmount = async () => {
    console.log("@@@@@", active);
    if (active) {
      try {
        const redeemContract = new Contract(
          REDEEM_ADDRESS,
          REDEEM_INFO.result,
          library.getSigner()
        );
        const data = await redeemContract.redeemBalanceMap(account);
        setAmount(parseFloat(formatUnits(data, 9)).toFixed(3));
        setMaxAmount(parseFloat(formatUnits(data, 9)).toFixed(3));
      } catch (err) {
        console.log(err);
        toast.error(err);
      }
    }
  };
  useEffect(() => {
    getAmount();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [active]);

  const redeemToken = async () => {
    if (Number(amount) > Number(maxAmount)) {
      console.log(amount, maxAmount);
      toast.error("Amount has to be less than or equal to max amount");
      return;
    }
    if (active) {
      try {
        setIsProcessing(true);
        const redeemContract = new Contract(
          REDEEM_ADDRESS,
          REDEEM_INFO.result,
          library.getSigner()
        );

        const res = await redeemContract.redeemToken(
          parseUnits(amount.toString(), 9)
        );
        await res.wait();
        toast.success("Success");
        setIsProcessing(false);
        getAmount();
      } catch (err) {
        console.log(err);
        toast.error(err);
        setIsProcessing(false);
      }
    } else {
      toast.error("Please connect your wallet first");
    }
  };
  return (
    <>
      <main className="main d-flex justify-content-center align-items-center main-redeem">
        {/* <img className="bg-redeem" src="/assets/img/sneak-left.png" alt="" /> */}
        <div className="redeem-group">
          <div>
            <input
              type="input"
              value={amount}
              onChange={(e) => setAmount(e.target.value)}
            />
            <button
              className="py-2 px-4 btn-redeem"
              disabled={isProcessing || amount === ""}
              onClick={() => {
                redeemToken();
              }}
            >
              Redeem(Max: {maxAmount})
            </button>
          </div>
          <p>
            If you have 10,000 tokens to claim please only claim 9999 to allow
            the transaction.
          </p>
        </div>
      </main>
    </>
  );
}
export default Redeem;
