import React from "react";
import { Link } from "react-router-dom";
// import LINK_ARTWORK from "assets/img/link-artwork.svg";
// import LINK_OWNER from "assets/img/link-owner.svg";
function Banner() {
  return (
    <div className="container">
      <div className="nft-text-center d-flex justify-center items-center nft-pt-60 mt-4">
        <div>
          <Link
            to="/explore"
            className="home__link-primary mr-4"
            rel="noopener noreferrer"
          >
            <img
              className=""
              src="assets/img/link-artwork.png"
              width="100%"
              alt="bg-main-left"
            />
          </Link>
        </div>
        <div>
          <Link to="/creators" rel="noopener noreferrer">
            <img
              className="ml-4"
              src="assets/img/link-owners.png"
              width="100%"
              alt="bg-main-right"
            />
          </Link>
        </div>
      </div>
    </div>
  );
}

export default Banner;
