import BtnBack from "components/Admin/btnBack";
import { firestore } from "../../firebase";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { toast } from "react-toastify";
import "styles/admin.css";

function AdminUsers() {
  const [searchName, setSearchName] = useState("");
  const [users, setUsers] = useState([]);

  const getUserLists = async (str) => {
    const res = await firestore
      .collection("users")
      .orderBy("nickName")
      .startAt(str)
      .endAt(str + "\uf8ff")
      .get();
    setUsers(
      !res.empty ? res.docs.map((x) => ({ id: x.id, ...x.data() })) : []
    );
  };
  const removeUser = async (id) => {
    await firestore.collection("users").doc(id).delete();
    setUsers(users.filter((x) => x.id !== id));
    toast.success("Successfully deleted");
  };

  useEffect(() => {}, []);
  return (
    <main className="main">
      <div className="container admin__container pt-5 d-flex flex-column justify-content-between">
        <div>
          <div className="admin__user__search mb-5">
            <p>Search user by his tag (example @nickname)</p>
            <input
              type="text"
              value={searchName}
              placeholder="Search..."
              onChange={(e) => {
                const str = e.target.value;
                if (!str.startsWith("@")) {
                  toast.error("Nick name should be started with @ symbol.");
                  return;
                }
                setSearchName(str);
                if (str.length > 2) getUserLists(str);
              }}
            />
          </div>

          <table className="admin__user__search mb-5">
            <tbody>
              {users.map((x, index) => (
                <tr key={`user-${index}`}>
                  <td>
                    <strong>{x.nickName}</strong>
                  </td>
                  {/* <td>
                    <div className="filter__checkboxes">
                      <input id="private" type="checkbox" name="private" />
                      <label className="sign__label" htmlFor="private">
                        Ban
                      </label>
                    </div>
                  </td>
                  <td>
                    <div className="filter__checkboxes">
                      <input id="private" type="checkbox" name="private" />
                      <label className="sign__label" htmlFor="private">
                        Revoke Minting
                      </label>
                    </div>
                  </td>
                  <td>
                    <div className="filter__checkboxes">
                      <input id="private" type="checkbox" name="private" />
                      <label className="sign__label" htmlFor="private">
                        Revoke Purchase
                      </label>
                    </div>
                  </td> */}
                  <td>
                    <Link to={`/admin/user/${x.id}`}>Manage NFTs &#10095;</Link>
                  </td>
                  <td
                    className="cursor-pointer"
                    onClick={() => {
                      removeUser(x.id);
                    }}
                  >
                    Remove User
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
        <BtnBack />
      </div>
    </main>
  );
}

export default AdminUsers;
