import NFTDropzone from "components/Dropzone";
import { firestore, storage } from "../../firebase";
import React, { useEffect, useState } from "react";
import "styles/admin.css";
import { toast } from "react-toastify";
import BtnBack from "components/Admin/btnBack";

function AdminAds() {
  const [topAds, setTopAds] = useState([]);
  const [sideAds, setSideAds] = useState([]);
  // eslint-disable-next-line no-unused-vars
  const [isProcessing, setIsProcessing] = useState(false);

  const uploadImg = async (img, isTop) => {
    try {
      setIsProcessing(true);
      const uploadCoverTask = await storage
        .ref(`/ads/${Date.now().toString() + img.name}`)
        .put(img);
      console.log(uploadCoverTask);
      if (uploadCoverTask.state !== "success") {
        toast.error("Failed to upload to firestore");
        return;
      }
      const imgCoverUrl = await uploadCoverTask.ref.getDownloadURL();
      console.log(imgCoverUrl);
      const res = await firestore.collection(isTop ? "adsTop" : "adsSide").add({
        isSelected: false,
        url: imgCoverUrl,
        storageUrl: uploadCoverTask.metadata.fullPath,
      });
      if (isTop)
        setTopAds([
          ...topAds,
          {
            id: res.id,
            url: imgCoverUrl,
            isSelected: false,
            storageUrl: uploadCoverTask.metadata.fullPath,
          },
        ]);
      else
        setSideAds([
          ...sideAds,
          {
            id: res.id,
            url: imgCoverUrl,
            isSelected: false,
            storageUrl: uploadCoverTask.metadata.fullPath,
          },
        ]);
    } catch (err) {
      console.log(err);
    }
  };

  const getAds = async () => {
    try {
      setIsProcessing(true);
      const res = await firestore.collection("adsTop").get();
      const data = res.docs.map((x) => ({
        id: x.id,
        url: x.data().url,
        isSelected: x.data().isSelected,
        storageUrl: x.data().storageUrl,
      }));
      setTopAds(data);

      const res1 = await firestore.collection("adsSide").get();
      const data1 = res1.docs.map((x) => ({
        id: x.id,
        url: x.data().url,
        isSelected: x.data().isSelected,
        storageUrl: x.data().storageUrl,
      }));

      setSideAds(data1);
    } catch (err) {
      console.log(err);
      setIsProcessing(false);
    }
  };

  const removeImg = async (id, storageUrl, isTop) => {
    const storageRes = await storage.ref(storageUrl).delete();
    console.log(storageRes);
    await firestore
      .collection(isTop ? "adsTop" : "adsSide")
      .doc(id)
      .delete();
    if (isTop) setTopAds(topAds.filter((x) => x.id !== id));
    else setSideAds(sideAds.filter((x) => x.id !== id));
  };

  useEffect(() => {
    getAds();
  }, []);

  return (
    <main className="main">
      <div className="container admin__container pt-5 d-flex flex-column justify-content-between">
        <div className="admin__ads row">
          <div className="col-md-6">
            <div className="admin__ads-tabHeader bg-purple">
              <h1>TOP AD</h1>
              <p>1350 * 280 pixels</p>
            </div>
            <div className="admin__ads-tabContent">
              {topAds.map((x, index) => (
                <div className="admin__ads-item" key={`topAds-${index}`}>
                  <div className="admin__ads-img">
                    <img src={x.url} alt="" />
                  </div>
                  <div
                    className="admin__ads-removeBtn"
                    onClick={() => {
                      removeImg(x.id, x.storageUrl, true);
                    }}
                  >
                    Remove
                  </div>
                </div>
              ))}
              <NFTDropzone
                nftType="image"
                isAd
                onChange={(newfile) => {
                  uploadImg(newfile, true);
                }}
              />
            </div>
          </div>
          <div className="col-md-6">
            <div className="admin__ads-tabHeader">
              <h1>SIDE AD</h1>
              <p>220 * 780 pixels</p>
            </div>
            <div className="admin__ads-tabContent side-ad">
              {sideAds.map((x, index) => (
                <div className="admin__ads-item-side" key={`sideAds-${index}`}>
                  <div className="admin__ads-img">
                    <img src={x.url} alt="" />
                  </div>
                  <div
                    className="admin__ads-removeBtn"
                    onClick={() => {
                      removeImg(x.id, x.storageUrl, false);
                    }}
                  >
                    Remove
                  </div>
                </div>
              ))}
              <NFTDropzone
                nftType="image"
                isAd
                onChange={(newfile) => {
                  uploadImg(newfile, false);
                }}
              />
            </div>
          </div>
        </div>
        <BtnBack />
      </div>
    </main>
  );
}

export default AdminAds;
