import React from "react";
import "./style.css";

function MinerFaq() {
  return (
    <div className="miner__faq">
      <div className="row">
        <div className="col-lg-6 pr-lg-5 miner__faq-item">
          <h3 className="bg-primary">What is Heroes of Crypton</h3>
          <p>
            Heroes of Crypton is a decentralized application built on the
            Binance Smart Chain that pays you 10% daily on average on your
            Heroes Of Crypton deposit into the Heroes of Crypton contract. The
            daily percentage ROI depends on several factors.
          </p>
        </div>
        <div className="col-lg-6 pl-lg-5 miner__faq-item">
          <h3 className="bg-secondary">
            Why is HOC different than any other miner?
          </h3>
          <p>
            This Heroes of crypton was designed to become the most sustainable
            miner powered by a unique auto-compounding feature. 25% from every
            withdrawal goes back into the contract. It’s a modified version of
            the well known passive income platforms that will ensure the
            longevity and sustainability of the miner.
          </p>
        </div>
        <div className="col-lg-6 pr-lg-5 miner__faq-item">
          <h3 className="bg-primary">
            What happens when my barrel becomes full?
          </h3>
          <p>
            Your Heroes will not stop working for you, however it’s beneficial
            if you compound (hire more heroes ) or claim (pocket) your Heroes
            divs within 24 hours. This is a way to keep people engaged.
          </p>
        </div>
        <div className="col-lg-6 pl-lg-5 miner__faq-item">
          <h3 className="bg-secondary">Can I take my initial deposit back?</h3>
          <p>
            Once you deposit, you can't take it back. You can only earn it back
            slowly over time by pocketing your Hero divs.
          </p>
        </div>
        <div className="col-lg-6 pr-lg-5 miner__faq-item">
          <h3 className="bg-primary">How long do the Heroes work for?</h3>
          <p>
            The Heroes will work for you indefinitely as long as the contract is
            filled with funds. They can not be sold or exchanged.
          </p>
        </div>
        <div className="col-lg-6 pl-lg-5 miner__faq-item">
          <h3 className="bg-secondary">How much are the fees?</h3>
          <p>
            On every deposit, 5% goes to the Liq pool . Every day this will be
            added back into liquidity to grow the Heroes Of Crypton /BNB
            liquidity pool!
          </p>
        </div>
        <div className="col-lg-6 pr-lg-5 miner__faq-item">
          <h3 className="bg-primary">How sustainable is Heroes of Crypton ?</h3>
          <p>
            HOC is designed to last for a long time by having lower daily
            returns compared to other similar dapps.
          </p>
        </div>
        <div className="col-lg-6 pl-lg-5 miner__faq-item">
          <h3 className="bg-secondary">What is the best strategy?</h3>
          <p>
            This can't be answered properly, as it highly depends on the
            lifespan of the contract. Generally, you would want to compound at
            least some of your earnings so you get additional heroes paying you
            more Heroes of Crypton everyday
          </p>
        </div>
        <div className="col-lg-6 pr-lg-5 miner__faq-item">
          <h3 className="bg-primary">
            Why do daily earnings percentages differ?
          </h3>
          <p>
            As previously stated, the daily percent returns depend on a lot of
            different things. As contract balance rises, the daily returns rise
            too for those that got in earlier. If the contract starts falling
            off in balance, there is a "contraction" on the daily payouts to
            ensure the longevity and sustainability of the contract. The degree
            of contraction itself depends on how much the contract balance is
            decreasing This applies to claiming and compounding too. If more
            people pocket their Heroes of Crypton than hiring more Heroes , the
            daily payouts could slow down depending on the ratio between the
          </p>
        </div>
        <div className="col-lg-6 pl-lg-5 miner__faq-item">
          <h3 className="bg-secondary">Why does the value of miners change?</h3>
          <p>
            Yet again, this is a mechanism to ensure the longevity of the
            contract. As more people deposit heroes get more and more expensive.
            If a user deposits early with an X amount, he will get a bigger
            share of the heroes compared to a user that deposits later with the
            same amount X. As the contract rises in balance, in general, Herloes
            get more expensive, although there are rare exceptions, such as if
            almost all users are pocketing and not compounding when the balance
            is not growing nor decreasing. Additionally, when the contract
            starts to decrease in balance, and depending on the degree, miners
            get less expensive and could become very cheap to give people an
            incentive to deposit more Heroes of Crypton tokens.
          </p>
        </div>
      </div>
    </div>
  );
}

export default MinerFaq;
