import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";

import Header from "pages/Header";
import Activity from "pages/Activity";
import AdminContract from "pages/AdminContract";
import Asset from "pages/Asset";
import Blog from "pages/Blog";
import Collection from "pages/Collection";
import Contact from "pages/Contact";
import Create from "pages/Create";
import Creator from "pages/Creator";
import Creators from "pages/Creators";
import Error from "pages/404";
import Explore from "pages/Explore";
import Faq from "pages/Faq";
// import Footer from "pages/Footer";
import Forgot from "pages/ForgotPassword";
import Main from "pages/Main";
import Miner from "pages/Miner";
import Privacy from "pages/Privacy";
import Profile from "pages/Profile";
import Redeem from "pages/Redeem";
import SignIn from "pages/SignIn";
import SignUp from "pages/SignUp";
import Staking from "pages/Staking";
import Token from "pages/Token";
// import Mint from "pages/Mint";
import Admin from "pages/Admin/AdminMain";
import AdminUser from "pages/Admin/adminUsers";
import AdminNFT from "pages/Admin/adminNFT";
import AdminAds from "pages/Admin/adminAds";
import AdminRedeem from "pages/Admin/adminRedeem";
import { useWeb3React } from "@web3-react/core";
import { ADMIN_ADDRESS } from "../constants";

function Routes(props) {
  const { account } = useWeb3React();

  return (
    <div>
      <BrowserRouter>
        <Header />
        <Switch>
          <Route exact path="/" component={Main} />
          <Route path="/explore/:cate" exact component={Explore} />
          <Route path="/explore" exact component={Explore} />
          <Route path="/activity" component={Activity} />
          <Route path="/item/:id" component={Asset} />
          <Route path="/token" component={Token} />
          <Route path="/faq" component={Faq} />
          <Route path="/blog" component={Blog} />
          <Route path="/contacts" component={Contact} />
          <Route path="/creators" component={Creators} />
          <Route path="/creator/:id" component={Creator} />
          <Route path="/collection" component={Collection} />
          <Route path="/create" component={Create} />
          <Route path="/signin" component={SignIn} />
          <Route path="/signup" component={SignUp} />
          <Route path="/forgot" component={Forgot} />
          <Route path="/404" component={Error} />
          <Route path="/privacy" component={Privacy} />
          <Route path="/profile" component={Profile} />
          <Route path="/adminContract" component={AdminContract} />
          <Route path="/staking" component={Staking} />
          <Route path="/miner" component={Miner} />
          {/* <Route path="/farm" component={Mint} /> */}
          <Route path="/redeem" component={Redeem} />
          {account === ADMIN_ADDRESS && (
            <>
              <Route path="/admin/user/:id" exact component={AdminNFT} />
              <Route path="/admin/user" exact component={AdminUser} />
              <Route path="/admin/ads" exact component={AdminAds} />
              <Route path="/admin/redeem" exact component={AdminRedeem} />
              <Route path="/admin" exact component={Admin} />
            </>
          )}
        </Switch>
      </BrowserRouter>
    </div>
  );
}

export default Routes;
