import React, { useState } from "react";
import "./style.css";

function Confirm() {
  const [cryptoAmount, setCryptoAmount] = useState(0);
  const [minerAmount, setMinerAmount] = useState(0);
  return (
    <div className="miner__confirm">
      <div className="row">
        <div className="col-lg-6 mb-5 mb-lg-0 relative">
          <div className="miner__confim-earch-left">
            {/* <img src="/assets/img/miner/" */}
          </div>
          <div className="mr-lg-5">
            <div className="miner__confirm--text">
              <p>Spend HOC</p>
              <input
                type="text"
                value={cryptoAmount}
                onChange={(e) => {
                  setCryptoAmount(e.target.value);
                }}
              />
            </div>
            <button className="bg-primary">CONFIRM</button>
          </div>
        </div>
        <div className="col-lg-6">
          <div className="ml-lg-5">
            <div className="miner__confirm--text">
              <p>Hire Miners</p>
              <input
                type="text"
                value={minerAmount}
                onChange={(e) => {
                  setMinerAmount(e.target.value);
                }}
              />
            </div>
            <button className="bg-secondary">CONFIRM</button>
          </div>
        </div>
      </div>
      <div className="row align-items-center miner__confirm-digging">
        <div className="col-xl-3">
          <img
            src="/assets/img/miner/robot.png"
            className="mx-auto flex"
            alt=""
          />
        </div>
        {/* <div className="col-1"></div> */}
        <div className="col-lg-4 col-xl-3 pl-lg-5 d-flex flex-column align-items-end">
          <p className="miner__confirm-lable">MINERS</p>
          <input type="text" value={0} className="miner__confirm-input" />
        </div>
        {/* <div className="col-1"></div> */}
        <div className="col-lg-4 col-xl-3 pl-lg-5 d-flex flex-column align-items-end">
          <p className="miner__confirm-lable">DIGGING</p>
          <input type="text" value={0} className="miner__confirm-input" />
        </div>
        {/* <div className="col-1"></div> */}
        <div className="col-lg-4 col-xl-3 pl-lg-5 d-flex flex-column align-items-end">
          <p className="miner__confirm-lable">MINED</p>
          <input type="text" value={0} className="miner__confirm-input" />
        </div>
      </div>
    </div>
  );
}

export default Confirm;
