import React from "react";
import { Link } from "react-router-dom";
function Category() {
  return (
    <div className="container">
      <div className="nft-text-center d-flex flex-column flex-lg-row justify-center items-center nft-pt-60 mt-4">
        <div className="mx-4 mb-4">
          <Link
            to="/explore/Champion"
            className="home__link-primary"
            rel="noopener noreferrer"
          >
            <img
              className=""
              src="assets/img/home/champions.png"
              width="100%"
              alt="bg-main-left"
            />
          </Link>
        </div>
        <div className="mx-4 mb-4">
          <Link
            to="/explore/Equipment"
            className="home__link-primary"
            rel="noopener noreferrer"
          >
            <img
              className=""
              src="assets/img/home/weapons.png"
              width="100%"
              alt="bg-main-left"
            />
          </Link>
        </div>
        <div className="mx-4 mb-4">
          <Link
            to="/explore/PowerUps"
            className="home__link-primary"
            rel="noopener noreferrer"
          >
            <img
              className=""
              src="assets/img/home/powerups.png"
              width="100%"
              alt="bg-main-left"
            />
          </Link>
        </div>
      </div>
    </div>
  );
}

export default Category;
