import BtnBack from "components/Admin/btnBack";
import { firestore } from "../../firebase";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { toast } from "react-toastify";
import "styles/admin.css";
import Card from "components/Card";

function AdminNFT() {
  const [user, setUser] = useState(null);
  const [nfts, setNfts] = useState([]);
  const { id } = useParams();

  const getNFTLists = async () => {
    const resUser = await firestore.collection("users").doc(id).get();
    setUser(resUser.exists ? resUser.data() : null);
    const res = await firestore
      .collection("nfts")
      .where("owner", "==", id)
      .get();
    let nfts_list = [];
    for (let i = 0; i < res.docs.length; i++) {
      const x = res.docs[i];
      const temp = x.data();
      const result = await (await fetch(temp.tokenURI)).json();
      const ite = { id: x.id, ...temp, ...result };
      nfts_list.push(ite);
    }
    setNfts(nfts_list);
  };
  const removeNFT = async (nftId) => {
    await firestore.collection("nfts").doc(nftId).delete();
    setNfts(nfts.filter((x) => x.id !== nftId));
    toast.success("Successfully deleted");
  };

  useEffect(() => {
    getNFTLists();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <main className="main">
      <div className="container admin__container pt-5 d-flex flex-column justify-content-between">
        {user && <p>{user.nickName}'s NFTs</p>}
        <div className="d-flex flex-wrap admin-nfts">
          {nfts.length > 0 &&
            nfts.map((x, index) => (
              <div className="" key={`adminNFT-${index}`}>
                <Card data={x} key={`card-${index}`} />
                <p
                  className="admin__nfts-remove"
                  onClick={() => removeNFT(x.id)}
                >
                  Remove
                </p>
              </div>
            ))}
        </div>
        <BtnBack url="/admin/user" />
      </div>
    </main>
  );
}

export default AdminNFT;
