import React, { useEffect, useState } from "react";
import { useWeb3React } from "@web3-react/core";
import "./style.css";
import { firestore } from "../../firebase";
import CardStaking from "components/CardStaking";
import { toast } from "react-toastify";

function MintAndReward() {
  const { account } = useWeb3React();

  const [nfts, setNfts] = useState([]);

  const getNfts = async () => {
    if (account) {
      const res = await firestore
        .collection("nfts")
        .where("owner", "==", account)
        // .where("owner", "==", "0x21CE5367cD34F13fAf22A105f682dd9ca72Bd877")
        .limit(8)
        .get();
      if (res.empty) return;
      const res1 = res.docs.map((x) => x.data());
      setNfts(res1);
    } else {
      toast.error("Please connect your wallet");
    }
  };

  useEffect(() => {
    getNfts();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [account]);

  return (
    <>
      <div className="MAR">
        <div className="MAR__lockportfolio">
          {/* <svg
            xmlns="http://www.w3.org/2000/svg"
            width="19.356"
            height="21.174"
            viewBox="0 0 19.356 21.174"
          >
            <g
              id="Icon_featherLock"
              dataName="Icon featherLock"
              transform="translate(-3 -1.5)"
            >
              <path
                id="Path_5867"
                dataName="Path 5867"
                d="M6.317,16.5H19.039a1.817,1.817,0,0,1,1.817,1.817v6.361A1.817,1.817,0,0,1,19.039,26.5H6.317A1.817,1.817,0,0,1,4.5,24.678V18.317A1.817,1.817,0,0,1,6.317,16.5Z"
                transform="translate(0 -5.322)"
                fill="none"
                stroke="#f05454"
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="3"
              />
              <path
                id="Path_5868"
                dataName="Path 5868"
                d="M10.5,11.178V7.543a4.543,4.543,0,0,1,9.087,0v3.635"
                transform="translate(-2.365)"
                fill="none"
                stroke="#f05454"
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="3"
              />
            </g>
          </svg>
          Lock your portfolio */}
        </div>
        <div className="row mb-4">
          <div className="col-md-4 pr-5">
            <div className="d-flex ml-auto flex-column justify-content-end">
              <h4>MINTED</h4>
              <input type="text" />
            </div>
          </div>
          <div className="col-md-3 d-flex px-lg-5">
            <div className="">
              <h3>Mint NFTs</h3>
              <p>
                You can mint NFT and get a random NFT collection that you can
                stake for rewards
              </p>
            </div>
          </div>
          <div className="col-md-5 pl-5">
            <div className="">
              <h3>Rewards Earned</h3>
              <div className="d-flex align-items-center justify-content-center justify-content-md-start">
                <img src="/assets/img/miner/logo.png" height="74px" alt="" />
                <h5 className="ml-3">2002126.25 HOC</h5>
              </div>
            </div>
          </div>
        </div>
        <div className="row align-items-end">
          <div className="col-md-4 pr-5">
            <div className="d-flex ml-auto flex-column justify-content-md-end">
              <h4>STAKED</h4>
              <input type="text" />
            </div>
          </div>
          <div className="col-md-3 d-flex px-lg-5">
            <button className="MAR__btn bg-secondary">MINT</button>
          </div>
          <div className="col-md-5 pl-lg-5">
            <button className="MAR__btn bg-primary">WITHDRAW</button>
          </div>
        </div>
      </div>
      <div className="nft__collection">
        <h3>Your NFT collection</h3>
        <diiv className="nft__collection-content">
          {nfts &&
            nfts.map((nft, index) => (
              <div className="px-2">
                <CardStaking data={nft} key={"stake-nft-" + index} />
              </div>
            ))}
        </diiv>
      </div>
    </>
  );
}

export default MintAndReward;
