import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { firestore } from "../../firebase";
import Loader from "../../components/Loader";
import Card from "components/Card";

function Featured() {
  const [maxNft, setMaxNft] = useState({});
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    getMaxNFT();
  }, []);

  const getMaxNFT = async () => {
    setLoading(true);
    const x = (
      await firestore
        .collection("nfts")
        .orderBy("likesCount", "desc")
        .limit(1)
        .get()
    ).docs[0];
    if (x?.exists) {
      const temp = x.data();
      fetch(temp.tokenURI)
        .then((res) => res.json())
        .then((result) => {
          const ite = { id: x.id, ...temp, ...result };
          // setFollow(ite.likes);
          setMaxNft(ite);
        })
        .catch((err) => {
          console.log(err);
          setMaxNft(null);
        });
    } else {
      setMaxNft(null);
    }

    setLoading(false);
  };

  // const increaseLikes = () => {
  //   if (account) {
  //     const user_index = follow.indexOf(account);
  //     if (maxNft.creator === account) {
  //       toast.error("You are a creator");
  //       return;
  //     }
  //     let temp = [...follow];
  //     if (user_index > -1) {
  //       temp[user_index] = temp[temp.length - 1];
  //       temp.pop();
  //     } else {
  //       temp = [...temp, account];
  //     }
  //     firestore
  //       .collection("nfts")
  //       .doc(maxNft.id)
  //       .update({ likes: temp, likesCount: temp.length })
  //       .then(() => {
  //         setFollow(temp);
  //         toast.success(`You ${user_index === -1 ? "" : "un"}follow NFT`);
  //       })
  //       .catch((err) => {
  //         toast.error(err);
  //       });
  //   } else {
  //     toast.error("Please connect your wallet first");
  //   }
  // };
  return (
    <div className="container">
      <div className="d-flex flex-column flex-lg-row align-items-center my-4 mx-2 mx-xl-5">
        <div className="w-full main__banner-title d-flex flex-column align-items-center align-items-lg-start mb-4 mb-lg-0">
          <Loader isLoading={loading} />
          {/* {!loading && <Preview data={maxNft} />} */}
          <h1>Discover, collect, and sell extraordinary NFTs</h1>
          {/* <p>Buy our unique NFTs to earn by playing our unique NFT game</p> */}
          <div className="d-flex flex-column flex-md-row">
            <Link to="/explorer" className="mr-5 mb-4 mb-md-0">
              <img src="/assets/img/btn/btn-explorer.png" alt="" />
            </Link>
            <Link to="/create">
              <img src="/assets/img/btn/btn-create.png" alt="" />
            </Link>
          </div>
        </div>
        <div className="w-500 pl-3 pl-xl-5">
          {/* <EthCard data={maxNft} /> */}
          {maxNft && (
            <Link to={`/item/$'{maxNft.id'}`}>
              <Card data={maxNft} />
            </Link>
          )}
          {/* <div className="d-flex justify-content-start">
            {maxNft?.saleType === "fix" ? "Buy Now" : "View Details"}
        </div> */}
        </div>
      </div>
    </div>
  );
}

export default Featured;
