import React from "react";
import { Link } from "react-router-dom";

function BtnBack(props) {
  return (
    <div className="mt-auto">
      <Link to={props.url ?? "/admin"}>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="61.582"
          height="22"
          viewBox="0 0 61.582 22"
        >
          <g
            id="Group_470"
            data-name="Group 470"
            transform="translate(-65.418 -1026)"
          >
            <text
              id="Back"
              transform="translate(81 1043)"
              fill="#141416"
              fontSize="18"
              fontFamily="Montserrat-Regular, Montserrat"
            >
              <tspan x="0" y="0">
                Back
              </tspan>
            </text>
            <path
              id="Icon_ionic-md-arrow-dropright"
              data-name="Icon ionic-md-arrow-dropright"
              d="M22.5,9l-9,9,9,9Z"
              transform="translate(51.918 1019)"
            />
          </g>
        </svg>
      </Link>
    </div>
  );
}

export default BtnBack;
