import Disclaimer from "components/Claimer";
import React from "react";
import { useWeb3React } from "@web3-react/core";
import "styles/footer.css";
import { toast } from "react-toastify";
import { ADMIN_ADDRESS } from "../../constants";
import { useHistory } from "react-router";
function Routes() {
  const { active, account } = useWeb3React();
  const history = useHistory();

  const gotoAdmin = () => {
    if (!active) {
      toast.error("Please connect your wallet, first");
      return;
    }
    if (account !== ADMIN_ADDRESS) {
      toast.error("Sorry, you are not admin");
      return;
    }

    history.push("/admin");
  };
  console.log(window.location.pathname);
  return (
    <footer className="footer">
      <div className="container justify-content-between">
        <div className="footer__logo">
          <img src="/assets/img/footer-logo.png" alt="" />
        </div>
        {/* <div className="footer-title ">
          <h1>MyNFTMarketplace</h1>
        </div> */}
        <div className="admin-panel d-flex flex-column align-items-center">
          {account === ADMIN_ADDRESS && (
            <button
              className="admin-btn mb-3"
              onClick={() => {
                gotoAdmin();
              }}
            >
              ADMIN LOGIN
            </button>
          )}
          <Disclaimer isFooter />
        </div>
      </div>
    </footer>
  );
}

export default Routes;
