import React from "react";
import { Link } from "react-router-dom";
import "styles/admin.css";

function Admin() {
  return (
    <main className="main">
      <div className="container admin__container d-flex flex-column flex-lg-row align-items-center justify-content-around">
        <div className="mx-lg-2 my-5 my-lg-0">
          <Link to="/admin/user" className="admin__btn">
            <img src="/assets/img/admin/1.png" alt="" />
          </Link>
        </div>
        <div className="mx-lg-2 my-5 my-lg-0">
          <Link to="/admin/ads" className="admin__btn">
            <img src="/assets/img/admin/0.png" alt="" />
          </Link>
        </div>
        <div className="mx-lg-2">
          <Link to="/admin/redeem" className="admin__btn">
            <img src="/assets/img/admin/2.png" alt="" />
          </Link>
        </div>
      </div>
    </main>
  );
}

export default Admin;
