import React from "react";
import "./style.css";

function MinerAbout() {
  return (
    <div className="d-flex justify-content-between miner__about flex-column flex-lg-row">
      <div className="miner__about--image pb-5 pb-lg-0">
        <img src="/assets/img/miner/chatbot.png" alt="" />
      </div>

      <div className="miner__about--content">
        <h3>
          HEROES OF CRYPTON MINER <span>COMING SOON</span>
        </h3>
        <p>
          Heroes of crypton miner is a decentralized application built on the
          binance smart chain That pays a 10% daily pay out on average on your
          HOC deposit
        </p>
        <div className="d-flex">
          <div className="d-flex flex-column align-items-center mx-auto mx-md-0">
            <div className="d-flex flex-column flex-md-row mb-3">
              <a
                href="/"
                target="_blank"
                className="miner__about--btn bg-primary"
                rel="noreferrer"
              >
                3,650% APR
              </a>
              <a
                href="/"
                target="_blank"
                className="miner__about--btn bg-secondary mt-5 mt-md-0 ml-md-5"
                rel="noreferrer"
              >
                10% Daily
              </a>
            </div>
            <img src="/assets/img/miner/down-star.png" alt="" />
          </div>
        </div>
      </div>
    </div>
  );
}

export default MinerAbout;
