import React, { useState, useEffect } from "react";
import { FaTimesCircle } from "react-icons/fa";
import { BsSearch } from "react-icons/bs";
import { useParams } from "react-router-dom";

import Card from "../../components/Card";
import Loader from "../../components/Loader";
import { client } from "../../utils/algolia";
import {
  PaymentList,
  MAX_LIMIT_FOR_BNB,
  MAX_LIMIT_FOR_TOKEN,
} from "../../constants";

import "styles/explore.css";
import { firestore } from "../../firebase";
import { Link } from "react-router-dom";
import Footer from "../Footer";

function Explore() {
  const { cate } = useParams();
  const [price, setPrice] = useState(100);
  const [cards, setCards] = useState([]);
  const [order, setOrder] = useState("new");
  const [type, setType] = useState("all");
  const [category, setCategory] = useState("all");
  const [searchText, setSearchText] = useState("");
  const [play, setPlay] = useState(true);
  const [showSale, setShowSale] = useState(false);
  const [pageNFT, setPageNFT] = useState(0);
  // eslint-disable-next-line no-unused-vars
  const [paymentType, setPaymentType] = useState("BNB");
  const [allUsers, setAllUsers] = useState(0);
  const [allNfts, setAllNfts] = useState(0);

  const getNFTList = async (isNew = false) => {
    try {
      setPlay(true);
      let algolia;
      if (order === "least") algolia = client.initIndex("NFTs_likesCount_asc");
      else if (order === "most")
        algolia = client.initIndex("NFTs_likesCount_desc");
      else if (order === "old") algolia = client.initIndex("NFTs_created_asc");
      else algolia = client.initIndex("NFTs");

      const filter = [];
      filter.push([`paymentType:${paymentType}`]);
      if (type !== "all") filter.push([`saleType:${type}`]);
      if (category !== "all") filter.push([`category:${category}`]);
      if (showSale) filter.push([`isSale:${showSale}`]);

      const res = await algolia.search(searchText, {
        hitsPerPage: 15,
        page: isNew ? 0 : pageNFT,
        facets: [
          "*",
          "category",
          "creator",
          "isSale",
          "owner",
          "saleType",
          "paymentType",
        ],
        facetFilters: filter,
        numericFilters: [`price<${price}`],
      });
      const lists = [];
      for (let i = 0; i < res.hits.length; i++) {
        let doc = res.hits[i];
        fetch(doc.tokenURI)
          .then((res) => {
            return res.json();
          })
          .then((result) => {
            const nft_data =
              result !== undefined && result !== null
                ? { id: doc.objectID, ...doc, ...result }
                : { id: doc.objectID, ...doc };
            lists.push(nft_data);
          })
          .catch((err) => console.log(err));
      }
      setTimeout(() => {
        setCards(isNew ? lists : [...cards, ...lists]);
      }, 1000);
      setPageNFT(isNew ? 1 : pageNFT + 1);
      setPlay(false);
    } catch (err) {
      console.log(err);
      setPlay(false);
    }
  };
  useEffect(() => {
    getNFTList(true);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    type,
    showSale,
    category,
    order,
    showSale,
    price,
    searchText,
    paymentType,
  ]);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(() => {
    const init = async () => {
      window.scrollTo(0, 0);
      let res = firestore.collection("nfts");
      const nfts = await res.get();
      for (let i = 0; i < nfts.docs.length; i++) {
        await firestore.collection("nfts").doc(nfts.docs[i].id).set(
          {
            paymentType: "BNB",
          },
          { merge: true }
        );
      }
    };
    init();
  }, []);
  const sliderChange = (e) => {
    setPrice(e.target.value);
  };
  const handleSearch = (e) => {
    setSearchText(e.target.value);
  };
  const handleReset = () => {
    setPrice(200);
    setSearchText("");
    setPrice(MAX_LIMIT_FOR_TOKEN);
    setOrder("new");
    setType("all");
    setCategory("all");
    setShowSale(false);
  };
  const getTotalData = async () => {
    const res1 = await firestore.collection("users").get();
    const res2 = await firestore.collection("nfts").get();
    setAllNfts(res2.docs.length);
    setAllUsers(res1.docs.length);
  };
  useEffect(() => {
    if (cate) setCategory(cate);
    getTotalData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <>
      <main className="main">
        <div className="container">
          <div className="row row--grid">
            {/* <BreadCrumb data={breadCrumb} /> */}
            <div className="col-12">
              <div className="main__title main__title--page nft-border-bottom pb-3">
                <div className="mb-3 mb-md-0">
                  <h1>Artworks</h1>
                  <div className="d-flex align-items-end">
                    <Link to="/explore" className="mr-3 active">
                      ARTWORKS({allNfts})
                    </Link>
                    <Link to="/creators">OWNERS({allUsers})</Link>
                  </div>
                </div>
                <div className="search-outline d-flex justify-content-center align-items-center">
                  <input
                    type="text"
                    placeholder="Search..."
                    className="search-input"
                    value={searchText}
                    onChange={(e) => handleSearch(e)}
                  />
                  <button className="email-btn">
                    <BsSearch onClick={() => getNFTList(true)} />
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div className="row row--grid">
            <div className="col-12 col-xl-3">
              <div className="slidecontainer mt-3">
                <label className="sign__label" htmlFor="subcategory">
                  PRICE RANGE <span style={{ fontSize: "20px" }}>{price} </span>{" "}
                  {paymentType}
                </label>
                <input
                  type="range"
                  min="1"
                  max={
                    paymentType === "BNB"
                      ? MAX_LIMIT_FOR_BNB
                      : MAX_LIMIT_FOR_TOKEN
                  }
                  className="slider"
                  id="myRange"
                  value={price}
                  onChange={(e) => sliderChange(e)}
                />
                <div className="d-flex justify-content-between">
                  <p className="nft-color-white">1 {paymentType}</p>
                  <p className="nft-color-white">
                    {paymentType === "BNB"
                      ? MAX_LIMIT_FOR_BNB
                      : MAX_LIMIT_FOR_TOKEN}{" "}
                    {paymentType}
                  </p>
                </div>
              </div>
              <div className="sign__group">
                <label className="sign__label" htmlFor="subcategory">
                  Payment Type:
                </label>
                <select
                  name="paymentType"
                  className="explore__select"
                  value={paymentType}
                  onChange={(e) => {
                    setPaymentType(e.target.value);
                  }}
                >
                  {PaymentList.map((payment, index) => (
                    <option value={payment.value} key={index}>
                      {payment.name}
                    </option>
                  ))}
                </select>
              </div>
              <div className="sign__group">
                <label className="sign__label" htmlFor="subcategory">
                  Filter by:
                </label>
                <select
                  name="subcategory"
                  className="explore__select"
                  value={order}
                  onChange={(e) => {
                    setOrder(e.target.value);
                  }}
                >
                  <option value="new">Newest</option>
                  <option value="old">Oldest</option>
                  <option value="most">Most liked</option>
                  <option value="least">Least liked</option>
                </select>
              </div>
              <div className="sign__group">
                <label className="sign__label" htmlFor="subcategory">
                  Sale Type:
                </label>
                <select
                  name="subcategory"
                  className="explore__select"
                  value={type}
                  onChange={(e) => {
                    setType(e.target.value);
                  }}
                >
                  <option value="all">All</option>
                  <option value="fix">Fixed</option>
                  <option value="auction">Auction</option>
                </select>
              </div>
              <div className="sign__group">
                <label className="sign__label" htmlFor="subcategory">
                  Categories:
                </label>
                <select
                  name="subcategory"
                  className="explore__select"
                  value={category}
                  onChange={(e) => {
                    setCategory(e.target.value);
                  }}
                >
                  <option value="all">All</option>
                  <option value="Champion">Champion</option>
                  <option value="Equipment">Equipment</option>
                  <option value="PowerUps">PowerUps</option>
                  <option value="Art">Art</option>
                  <option value="Music">Music</option>
                  <option value="Film">Film</option>
                  <option value="Sports">Sports</option>
                  <option value="Education">Education</option>
                  <option value="Photography">Photography</option>
                  <option value="Games">Games</option>
                  <option value="Other">Other</option>
                </select>
              </div>
              <div className="filter__checkboxes mb-3">
                <input
                  id="type5"
                  type="checkbox"
                  name="type5"
                  checked={showSale}
                  onChange={(e) => {
                    setShowSale(e.target.checked);
                  }}
                />
                <label htmlFor="type5" style={{ fontSize: 18, color: "black" }}>
                  On Sale
                </label>
              </div>
              <div onClick={() => handleReset()} className="nft-pointer">
                <p className="reset m-0">
                  <FaTimesCircle className="mr-2 text-red" />
                  Reset filter
                </p>
              </div>
            </div>

            <div className="col-12 col-xl-9">
              <div className="row row--grid relative">
                {cards.map((card, index) => (
                  <div
                    className="col-12 col-sm-6 col-lg-4"
                    key={`card-${index}`}
                  >
                    <Card data={card} />
                  </div>
                ))}

                <Loader isLoading={play} />
                <div className="col-12 d-flex justify-center">
                  <button
                    className="asset__btn asset__btn--full asset__btn--clr"
                    style={{
                      width: 200,
                      margin: "50px auto",
                      height: 70,
                      background: "#DDDDDD",
                      color: "#23262F",
                    }}
                    onClick={() => {
                      getNFTList(false);
                    }}
                  >
                    Load More
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>
      <Footer />
    </>
  );
}

export default Explore;
