import MinerAbout from "components/Miner/about";
import Confirm from "components/Miner/confirm";
import MinerFaq from "components/Miner/faq";
import MinerFooter from "components/Miner/footer";
import React from "react";
function Miner() {
  return (
    <>
      <main className="main">
        <div className="container">
          <MinerAbout />
          <Confirm />
          <MinerFaq />
        </div>
      </main>
      <MinerFooter miner />
    </>
  );
}
export default Miner;
