import React, { useState } from "react";
import { toast } from "react-toastify";
import { firestore } from "../../firebase";
import { NFTStorage } from "nft.storage";
import { NFTStorageKey } from "../../constants/index";
import ipfs from "utils/ipfsApi.js";
import moment from "moment";

const client = new NFTStorage({ token: NFTStorageKey });

function SetMarketFee() {
  const [isProcessingMint, setIsProcessingMint] = useState(false);
  const [startNum, setStartNum] = useState(0);
  // const [buffer, setBuffer] = useState(null);
  // const [buffers, setBuffers] = useState([]);

  const getFile = (file, i) => {
    console.log(file);
    const reader = new FileReader();
    reader.onabort = () => console.log("file reading was aborted");
    reader.onerror = () => console.log("file reading has failed");
    reader.onload = async () => {
      // Do whatever you want with the file contents
      const binaryStr = reader.result;
      // return binaryStr;
      // setBuffers([...buffers, binaryStr]);
      await mintNFTs(binaryStr, i);
      // console.log(buffers.length);

      // setBuffer(binaryStr);
    };
    reader.readAsArrayBuffer(file);
  };

  const mintNFTs = async (e, i) => {
    try {
      setIsProcessingMint(true);
      console.log(e);
      const ress = await firestore
        .collection("nfts")
        .where("name", "==", `#${parseInt(startNum) + i + 1}`)
        .get();
      if (ress.empty) {
        // for (let i = 0; i < buffers.length; i++) {
        const result = await ipfs.files.add(Buffer.from(e));
        const cid = await client.storeDirectory([
          new File(
            [
              JSON.stringify({
                name: `#${parseInt(startNum) + i + 1}`,
                description: `CryptoNite NFT #${parseInt(startNum) + i + 1}`,
                creator: "0x21CE5367cD34F13fAf22A105f682dd9ca72Bd877",
                type: "image",
                category: "Champion",
                royalties: 10,
                image: `https://ipfs.io/ipfs/${result[0].hash}`,
                // imageAttach: imgAttach
                //   ? `https://ipfs.io/ipfs/${imgAttach[0].hash}`
                //   : null,
                imageAttach: null,
              }),
            ],
            "metadata.json"
          ),
        ]);
        if (cid) {
          const tokenURI = `https://ipfs.io/ipfs/${cid}/metadata.json`;
          await firestore.collection("nfts").add({
            tokenId: 0,
            tokenURI,
            ownerAvatar: "/assets/img/avatars/avatar.jpg",
            owner: "0x21CE5367cD34F13fAf22A105f682dd9ca72Bd877",
            creator: "0x21CE5367cD34F13fAf22A105f682dd9ca72Bd877",
            price: 0.25,
            paymentType: "BNB",
            isSale: true,
            saleType: "fix",
            auctionLength: 0,
            auctionCreator: null,
            time: 0,
            likes: [],
            likesCount: 0,
            created: moment().valueOf(),
            type: "image",
            category: "Champion",
            name: `#${parseInt(startNum) + i + 1}`,
            description: `CryptoNite NFT #${parseInt(startNum) + i + 1}`,
          });
        }
        console.log("Create NFT", i);
      } else {
        console.log("exist", i);
      }
      // }
      // toast.success("all done");
      setIsProcessingMint(false);
    } catch (err) {
      console.log(err);
      toast.error("Failed");
    }
  };
  return (
    <div className="col-12 col-xl-4 col-md-4">
      <div className="asset__info">
        <h2 className="text-white">Mint random NFTs</h2>
        <input
          type="text"
          value={startNum}
          onChange={(e) => {
            setStartNum(e.target.value);
          }}
        />
        <input
          type="file"
          multiple="multiple"
          onChange={async (e) => {
            const files = e.target.files;
            console.log(files);
            if (files.length > 0) {
              for (let i = 0; i < files.length; i++) {
                const element = files[i];
                await getFile(element, i);
              }
              // files.forEach((element) => {
              //   getFile(element);
              // });
            }
          }}
        />
        <div className="d-flex justify-center">
          <button
            className="asset__btn asset__btn--clr height-sm col-12"
            onClick={mintNFTs}
          >
            {isProcessingMint ? "MINTING..." : "Mint NFTs"}
          </button>
        </div>
      </div>
    </div>
  );
}

export default SetMarketFee;
