import BtnBack from "components/Admin/btnBack";
import { database } from "../../firebase";
import React, { useEffect, useState } from "react";
import { toast } from "react-toastify";
import "styles/admin.css";
import { shortenHex } from "utils/helper";
import { useWeb3React } from "@web3-react/core";
import { Contract } from "@ethersproject/contracts";
import {
  REDEEM_ADDRESS,
  BASE_TOKENADDRESS,
  ADMIN_TOKENADDRESS,
} from "../../constants";
import REDEEM_INFO from "../../constants/redeem.json";
import ERC20ABI from "../../constants/ERC20ABI.json";
import { formatUnits, parseUnits } from "@ethersproject/units";

function AdminRedeem() {
  const { active, library } = useWeb3React();
  const [isProcessing, setIsProcessing] = useState(false);
  const [records, setRecords] = useState([]);
  // const [withDrawAddress, setWithDrawAddress] = useState("");
  // const [withDrawAmount, setWithDrawAmount] = useState("");
  const [totalAmount, setTotalAmount] = useState(0);
  const [tokenContractBalance, setTokenContractBalance] = useState(0);
  const [rate, setRate] = useState(100);

  const getRedeemLists = async () => {
    const ref = database.ref();
    ref.on("value", function (snapshot) {
      console.log(snapshot, snapshot.exists());
      if (snapshot.exists()) {
        const data = snapshot.val();
        const keys = Object.keys(data);
        setRecords(
          keys.filter((x) => data[x].isSeen === false).map((x) => data[x])
        );
      }
    });
    const amountInContract = await redeemContract.totalRedeemAmount();
    const me = await redeemContract.redeemBalanceMap(ADMIN_TOKENADDRESS);
    setTotalAmount(
      parseFloat(formatUnits(amountInContract, 9) - formatUnits(me, 9)).toFixed(
        3
      )
    );
    // const tokenAmount = await redeemContract.getTotalBalance();
    // console.log("###########", tokenAmount);
  };

  const getTokenContractBalance = async () => {
    console.log(`listening for Transfer...`);
    const contract = new Contract(
      BASE_TOKENADDRESS,
      ERC20ABI,
      library.getSigner()
    );
    const temp = await contract.balanceOf(REDEEM_ADDRESS);
    setTokenContractBalance(parseFloat(formatUnits(temp, 9)).toFixed(3));
  };

  const redeemContract = new Contract(
    REDEEM_ADDRESS,
    REDEEM_INFO.result,
    library.getSigner()
  );

  useEffect(() => {
    getRedeemLists();
    getTokenContractBalance();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleConfirm = async () => {
    if (active) {
      try {
        setIsProcessing(true);
        const redeemContract = new Contract(
          REDEEM_ADDRESS,
          REDEEM_INFO.result,
          library.getSigner()
        );
        const candidates = [];
        const amounts = [];
        records.forEach((record) => {
          candidates.push(record.userWallet);
          amounts.push(
            parseUnits((record.userCoinAmount * parseFloat(rate)).toString(), 9)
          );
        });

        const res = await redeemContract.addRedeemCandidates(
          candidates,
          amounts
        );
        await res.wait();
        await removeAll();
        toast.success("Success");
        setIsProcessing(false);
      } catch (err) {
        console.log(err);
        toast.error(err);
        setIsProcessing(false);
      }
    } else {
      toast.error("Please connect your wallet first");
    }
  };
  const removeAll = async () => {
    for (let i = 0; i < records.length; i++) {
      await database.ref(records[i].localId).remove();
    }
    setRecords([]);
  };
  const removeRecord = async (id) => {
    try {
      removeAll();
      // const ref = await database.ref(id);
      // console.log(id, ref)
      // ref.remove();
      // toast.success('Success');
    } catch (err) {
      console.log(err);
      toast.error(err);
    }
  };

  // const withdrawToken = async () => {
  //   if (active) {
  //     try {
  //       setIsProcessing(true);
  //       const redeemContract = new Contract(
  //         REDEEM_ADDRESS,
  //         REDEEM_INFO.result,
  //         library.getSigner()
  //       );

  //       const res = await redeemContract.withdrawByOwner(
  //         withDrawAddress,
  //         BASE_TOKENADDRESS,
  //         parseUnits(withDrawAmount.toString(), 9)
  //       );
  //     } catch (err) {
  //       console.log(err);
  //       toast.error(err);
  //     }
  //   } else {
  //     toast.error("Please connect wallet first.");
  //   }
  // };
  return (
    <main className="main">
      <div className="container admin__container pt-5 d-flex flex-column justify-content-between">
        <div className="">
          <p>Token Amount In Redeem Contract: {tokenContractBalance} HOC</p>
          <p>Withdrawable Amount: {totalAmount} HOC</p>
        </div>
        <div className="d-flex justify-content-end">
          <button
            onClick={() => {
              handleConfirm();
            }}
            disabled={isProcessing || records.length === 0}
            className="sign__btn"
            style={{ width: 150, margin: "0 0 30px" }}
          >
            Confirm
          </button>
        </div>
        <div className="d-flex justify-content-end align-items-center mb-3">
          Rate:
          <input
            className="rate-text"
            type="text"
            value={rate}
            onChange={(e) => {
              setRate(e.target.value);
            }}
          />
        </div>
        <div>
          <table className="admin__user__search mb-5">
            <thead>
              <tr>
                <th>Wallet address</th>
                <th>Coin</th>
                <th>Start</th>
                <th>Duration</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
              {records.map((x, index) => (
                <tr className="" key={"record-" + index}>
                  <td>
                    <div className="filter__checkboxes">
                      <input
                        id={"private" + x.localId}
                        type="checkbox"
                        name={"private" + x.localId}
                      />
                      <label
                        className="sign__label"
                        htmlFor={"private" + x.localId}
                      >
                        {shortenHex(x.userWallet)}
                      </label>
                    </div>
                  </td>
                  <td>{x.userCoinAmount * 100}</td>
                  <td>{x.timeStamp}</td>
                  <td>{x.timeRan}</td>
                  <td>
                    <button
                      className="profile__btn"
                      onClick={() => {
                        removeRecord(x.localId);
                      }}
                    >
                      Remove
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {/* <div className="d-flex align-items-center justify-content-center mb-5 redeem-withdraw-box">
          Address:
          <input
            className="sign__input mb-4"
            value={withDrawAddress}
            onChange={(e) => {
              setWithDrawAddress(e.target.value);
            }}
          />
          Amount:
          <input
            className="sign__input mb-4"
            value={withDrawAmount}
            onChange={(e) => {
              setWithDrawAmount(e.target.value);
            }}
          />
          <button
            className="sign__btn m-0"
            disabled={withDrawAmount === "" || withDrawAddress === ""}
            onClick={() => {
              withdrawToken();
            }}
          >
            Withdraw
          </button>
        </div> */}

        <BtnBack />
      </div>
    </main>
  );
}

export default AdminRedeem;
